body {
  margin: 0;
}

.stars__star {
  background-image: url("images/star.svg");
  background-repeat: no-repeat;
  background-position: center;
  height: 16px;
  width: 16px;
  margin: 0 2px;
}

.stars--0,
.stars--1,
.stars--2,
.stars--3,
.stars--4,
.stars--5 {
  display: flex;
}

.stars--1 .stars__star:nth-child(1),
.stars--2 .stars__star:nth-child(-n + 2),
.stars--3 .stars__star:nth-child(-n + 3),
.stars--4 .stars__star:nth-child(-n + 4),
.stars--5 .stars__star:nth-child(n) {
  background-image: url("images/star-active.svg");
}

.stars--0 .stars__star:nth-child(1),
.stars--1 .stars__star:nth-child(1),
.stars--2 .stars__star:nth-child(1),
.stars--3 .stars__star:nth-child(1),
.stars--4 .stars__star:nth-child(1),
.stars--5 .stars__star:nth-child(1) {
  margin: 0 2px 0 0;
}
